import { h } from "preact"
import Autism from "./autism"
import ADHD from "./adhd"
import Dyslexic from "./dyslexia"
interface props {
   test: string
}
const test = ({ test }: props) => {
   let toReturn = (
      <h1>
         Test not found
      </h1>
   )
   switch(test){
      case "autism":
         toReturn = <Autism />
         break
      case "adhd":
         toReturn = <ADHD />
         break
      case "dyslexia":
         toReturn = <Dyslexic />
         break
   }
   return toReturn
}
export default test