import { h } from 'preact';
import { Route, Router } from 'preact-router';

import Header from './header';

// Code-splitting is automated for `routes` directory
import Home from '../routes/home';
import Tests from "../routes/disabilitytest";
import TestsForSpecific from "../routes/disabilitytest/tests"
const App = () => (
	<div id="app">
		<Header />
		<main>
			<Router>
				<Route path="/real-disability-test/" component={Home} />
            <Route path="/real-disability-test/test/" component={Tests} />
            <Route path="/real-disability-test/test/:test" component={TestsForSpecific} />
			</Router>
		</main>
	</div>
);

export default App;
