import { h } from "preact"
import { useState } from "preact/hooks"
import style from "./style.css"
const ADHD = () => {
   const [started, changeStart] = useState(false)
   const [yesOrNo, testResultChange] = useState(0)
   return (
      <div id="parentDiv">
         <div id="test" style={started ? "display: none" : "text-align: center"}>
            <h1>Click below to get started</h1>
            <button 
               id="start"
               class={style.bigButton}
               onClick={() => changeStart(true)}
            >
               Start
            </button>
         </div>
         <div id="the-test" style={(started && yesOrNo === 0) ? "text-align: center" : "display: none"}>
            <h1>Do you have ADHD</h1>
            <button
               id="yes"
               class={`${style.bigButton} ${style.greenButton}`}
               onClick={() => testResultChange(1)}
            >
               Yes
            </button>
            <a>  </a>
            <button
               id="no"
               class={`${style.bigButton} ${style.redButton}`}
               onClick={() => testResultChange(2)}
            >
               No
            </button>
            <h3>Question 1 out of 20</h3>
         </div>
         <div id="results" style={(yesOrNo !== 0) ? "text-align: center" : "display: none"}>
            <h2>
               Our results indicate that:
            </h2>
            <h1 style={(yesOrNo !== 1) ? "color: green" : "color: red"}>
               {
                  (yesOrNo !== 1) ? "You don't have ADHD" : "You have ADHD"
               }
            </h1>
         </div>
      </div>
   )
}
export default ADHD